import React, {Component} from 'react';
import PropTypes from 'prop-types';
import {withStyles} from '@material-ui/core/styles';
import classnames from 'classnames';
import Card from '@material-ui/core/Card';
import CardHeader from '@material-ui/core/CardHeader';
import CardMedia from '@material-ui/core/CardMedia';
import CardContent from '@material-ui/core/CardContent';
import CardActions from '@material-ui/core/CardActions';
import Collapse from '@material-ui/core/Collapse';
import Avatar from '@material-ui/core/Avatar';
import IconButton from '@material-ui/core/IconButton';
import Typography from '@material-ui/core/Typography';
import red from '@material-ui/core/colors/red';
import FavoriteIcon from 'material-ui-icons/Favorite';
import ShareIcon from 'material-ui-icons/Share';
import ExpandMoreIcon from 'material-ui-icons/ExpandMore';
import MoreVertIcon from 'material-ui-icons/MoreVert';
import axios from "axios";
import config from 'react-global-configuration';
import CircularProgress from "@material-ui/core/CircularProgress";
import ButtonBase from "@material-ui/core/ButtonBase";
import Link from "@material-ui/core/Link";
import CardActionArea from "@material-ui/core/CardActionArea";
import Cookies from "universal-cookie";
import Button from "@material-ui/core/Button";
import CloseIcon from 'material-ui-icons/Close';
import Snackbar from "@material-ui/core/Snackbar";
import Image from 'material-ui-image'
var querystring = require('querystring');

const cookies = new Cookies();

const styles = theme => ({
    card: {
        width: 300,
    },
    media: {
        height: 0,
        paddingTop: '56.25%', // 16:9
    },
    actions: {
        display: 'flex',
    },
    expand: {
        transform: 'rotate(0deg)',
        marginLeft: 'auto',
        transition: theme.transitions.create('transform', {
            duration: theme.transitions.duration.shortest,
        }),
    },
    expandOpen: {
        transform: 'rotate(180deg)',
    },
    avatar: {
        backgroundColor: red[500],
    },
    progress: {
        margin: theme.spacing.unit * 2,
        display: "",
    },
    cardAction: {
        display: 'block',
        textAlign: 'initial',
    },
    snackbar: {
        position: 'absolute',
    }
});


class Product extends Component {
    constructor(props) {
        super(props);
        this.state = {
            data: {},
            user: {},
            auth: false,
            addBtnTxt: "Enable",
            messageBox: {
                text: "",
                open: false
            },
            removeBtnDisabled: true,
        };
        this.getProduct(this.props.id);
        this.getUser();
    }

    async getProduct(id) {
        var response = await axios.get(config.get("apiUrl") + '/products/' + id);
        this.setState({data: response.data, addBtnTxt: response.data.accepted.accepted ? "Disable" : "Enable"});
        console.log(response.data);
    }

    async getUser() {
        if (cookies.get("token") !== undefined) {
            var t = this;
            t.setState({auth: true});
            var response = await axios.get(config.get("apiUrl") + '/users?token=' + cookies.get("token"));
            t.setState({user: response.data});
            var d = this.state.user.userType !== "ADMIN";
            if (d === true) {
                d = this.state.data.createdBy !== this.state.user.email;
            }

            t.setState({ removeBtnDisabled: d })
            //this.state.user.userType !== "ADMIN" || this.state.data.createdBy !== this.state.user.email
        }
    }

    addBtn = () => {
        var t = this;

        const headers = {
            'Content-Type': 'application/x-www-form-urlencoded;charset=UTF-8'
        };
        if (this.state.addBtnTxt === "Enable") {
            axios.put(config.get("apiUrl") + '/product/' + t.props.id + '?token=' + cookies.get("token"),
                querystring.stringify({
                    data: JSON.stringify({ accepted: { accepted: true, by: t.state.user.email } }),
                }),headers).then(function (response) {
                var ret = response.data;

                console.log(t.state.dialogOpenUser);
                console.log(t.state.dialogInfo);
                console.log(ret);

                t.getProduct(t.props.id);

                //t.setState({ messageBox: {open: true, text: "Enabled"} });
                t.props.messageBox({open: true, text: "Enabled"});
            });
        } else {
            axios.put(config.get("apiUrl") + '/product/' + t.props.id + '?token=' + cookies.get("token"),
                querystring.stringify({
                    data: JSON.stringify({ accepted: { accepted: false, by: t.state.user.email } }),
                }),headers).then(function (response) {
                var ret = response.data;

                console.log(t.state.dialogOpenUser);
                console.log(t.state.dialogInfo);
                console.log(ret);

                t.getProduct(t.props.id);

                //t.setState({ messageBox: {open: true, text: "Disabled"} });
                t.props.messageBox({open: true, text: "Disable"});

            });
        }
    };

    removeBtn = () => {

    };

    editBtn = () => {
        window.location.pathname = "productEdit/" + this.props.id;
    };

    render() {
        const {classes} = this.props;
        var content = (<CircularProgress className={styles.progress}/>);

        if (this.state.data.image) {
            if (this.state.auth === true) {
                while (this.state.user.email === undefined) {}
            }
            content = (
                <div>
                    <Typography component="p" noWrap color="inherit">
                        Price: {this.state.data.price},-
                    </Typography>
                    <Typography component="p" noWrap color="inherit">
                        Amount: {this.state.data.amount}
                    </Typography>
                </div>
            )
        }

        return (
            <div>
                <Card className={classes.card}>
                    <CardActionArea
                        onClick={event => {
                            window.location.pathname = "product/" + this.props.id;
                        }}>
                        <CardHeader
                            title={this.state.data.title}
                        />
                        <CardMedia
                            className={classes.media}
                            image={config.get("apiUrl") + this.state.data.image}
                        />
                        <CardContent>
                            {content}
                        </CardContent>
                    </CardActionArea>

                    {this.state.user.email ? (<CardActions>
                        <Button size="small" color="primary" disabled={this.state.user.userType !== "ADMIN"} onClick={this.addBtn}>
                            {this.state.addBtnTxt}
                        </Button>
                        <Button size="small" color="primary" disabled={this.state.removeBtnDisabled} onClick={this.removeBtn}>
                            Remove
                        </Button>
                        <Button size="small" color="primary" disabled={this.state.removeBtnDisabled} href={"productEdit/" + this.props.id}>
                            Edit
                        </Button>
                    </CardActions>) : (<div/>)}
                </Card>


                <Snackbar
                    className={classes.snackbar}
                    anchorOrigin={{
                        vertical: 'bottom',
                        horizontal: 'left',
                    }}
                    open={this.state.messageBox.open}
                    autoHideDuration={6000}
                    onClose={(event, reason) => {
                        if (reason === 'clickaway') {
                            return;
                        }

                        this.setState({ messageBox: { open: false } });
                    }}
                    ContentProps={{
                        'aria-describedby': 'message-id',
                    }}
                    message={<span id="message-id">{this.state.messageBox.text}</span>}
                    action={[
                        <IconButton
                            key="close"
                            aria-label="Close"
                            color={"#fafafa"}
                            className={styles.close}
                            onClick={(event, reason) => {
                                if (reason === 'clickaway') {
                                    return;
                                }

                                this.setState({ messageBox: { open: false } });
                            }}
                        >
                            <CloseIcon color={"#fafafa"} />
                        </IconButton>,
                    ]}
                />
            </div>
        );
    }
}

Product.propTypes = {
    classes: PropTypes.object.isRequired,
};

export default withStyles(styles)(Product);