import React, {Component} from 'react';
import Cookies from 'universal-cookie';
import Fab from '@material-ui/core/Fab';
import AddIcon from 'material-ui-icons/Add';
import Button from '@material-ui/core/Button';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';
import Paper from '@material-ui/core/Paper';
import Draggable from 'react-draggable';
import withStyles from "@material-ui/core/styles/withStyles";
import TextField from "@material-ui/core/TextField";
import Divider from "@material-ui/core/es/Divider/Divider";
import Select from "../ManageUsers/ManageUsers";
import Product from "../Product/Product";
import axios from "axios";
import config from "react-global-configuration";
import CircularProgress from "@material-ui/core/CircularProgress";
import GridList from "@material-ui/core/GridList";
import GridListTile from "@material-ui/core/GridListTile";
import Grid from "@material-ui/core/Grid";
import Tooltip from "@material-ui/core/Tooltip";
import IconButton from "@material-ui/core/IconButton";
import Snackbar from "@material-ui/core/Snackbar";
import CloseIcon from 'material-ui-icons/Close';
import Image from 'material-ui-image'

function PaperComponent(props) {
    return (
        <Draggable>
            <Paper {...props} />
        </Draggable>
    );
}

const cookies = new Cookies();

const styles = theme => ({
    fab: {
        position: 'absolute',
        bottom: theme.spacing.unit * 2,
        right: theme.spacing.unit * 2,
    },
    textField: {
        marginLeft: theme.spacing.unit,
        marginRight: theme.spacing.unit,
        width: 200,
    },
    gridList: {
        width: "100%",
        height: "100%",
    },
});

class Home extends Component {
    constructor(props) {
        super(props);
        this.state = {
            addProductDialog: {
                open: false,
                title: '',
            },
            data: [],
            messageBox: {
                text: "",
                open: false
            },
            user: {},
        };
        this.getUser();
        this.getProduct();
    }

    async getProduct() {
        var response = await axios.get(config.get("apiUrl") + '/products');
        this.setState({data: response.data});
        console.log(response.data);
    }

    addProduct = () => {
        this.setState({addProductDialog: {open: true}});
    };

    handleAddProductDialogClose = () => {
        this.setState({addProductDialog: {open: false}});
    };

    handleAddProduct = () => {
        this.setState({addProductDialog: {open: true}});
    };

    renderButton() {
        const {classes, theme} = this.props;
        if (cookies.get("token") !== undefined) {
            return (<Tooltip title={"Add product"} placement="top">
                <Fab color="primary" aria-label="Add" className={classes.fab} onClick={() => {
                    window.location.pathname = "addProduct"
                }}>
                    <AddIcon color={"#fafafa"}/>
                </Fab>
            </Tooltip>)
        } else {
            return (<div/>);
        }
    }

    async getUser() {
        if (cookies.get("token") !== undefined) {
            var t = this;
            t.setState({auth: true});
            var response = await axios.get(config.get("apiUrl") + '/users?token=' + cookies.get("token"));
            t.setState({user: response.data});
        }
    }

    render() {
        const {classes, theme} = this.props;

        var content = (<CircularProgress className={styles.progress}/>);
        if (this.state.data[0]) {
            content = (this.state.data.map((data, id) => {
                console.log(this.props.user);
                var d = this.state.user.userType !== undefined;
                console.log(d);
                if (d === false) {
                    d = data.accepted.accepted;
                }
                console.log(d);

                if (d) {
                    return (
                        <Grid item xs key={id} style={{margin: 10}}>
                            <Product id={data._id} userType={this.state.userType} messageBox={(info) => {this.setState({messageBox:info})}}/>
                        </Grid>
                    )
                }
            }));
        }

        return (
            <div style={{"margin": "10px"}}>

                <div>
                    <Grid container className={classes.gridList} spacing={24}>
                        {content}
                    </Grid>
                </div>

                <Snackbar
                    anchorOrigin={{
                        vertical: 'bottom',
                        horizontal: 'left',
                    }}
                    open={this.state.messageBox.open}
                    autoHideDuration={6000}
                    onClose={(event, reason) => {
                        if (reason === 'clickaway') {
                            return;
                        }

                        this.setState({ messageBox: { open: false } });
                    }}
                    ContentProps={{
                        'aria-describedby': 'message-id',
                    }}
                    message={<span id="message-id">{this.state.messageBox.text}</span>}
                    action={[
                        <IconButton
                            key="close"
                            aria-label="Close"
                            color={"#fafafa"}
                            className={styles.close}
                            onClick={(event, reason) => {
                                if (reason === 'clickaway') {
                                    return;
                                }

                                this.setState({ messageBox: { open: false } });
                            }}
                        >
                            <CloseIcon color={"#fafafa"} />
                        </IconButton>,
                    ]}
                />
            </div>
        );
    }
}

//export default Home;

export default withStyles(styles, {withTheme: true})(Home);