import React, {Component} from 'react';
import Cookies from 'universal-cookie';

import {withStyles} from '@material-ui/core/styles';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import CircularProgress from '@material-ui/core/CircularProgress';
import Button from '@material-ui/core/Button';
import Dialog from '@material-ui/core/Dialog';
import MuiDialogTitle from '@material-ui/core/DialogTitle';
import MuiDialogContent from '@material-ui/core/DialogContent';
import MuiDialogActions from '@material-ui/core/DialogActions';
import IconButton from '@material-ui/core/IconButton';
import Typography from '@material-ui/core/Typography';
import Tooltip from '@material-ui/core/Tooltip';
import TableSortLabel from '@material-ui/core/TableSortLabel';
import classNames from 'classnames';
import PropTypes from 'prop-types';
//import { AutoSizer, Column, SortDirection, Table } from 'react-virtualized';
import axios from "axios";
import CloseIcon from 'material-ui-icons/Close';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import Checkbox from '@material-ui/core/Checkbox';
import Select from "@material-ui/core/es/Select/Select";
import MenuItem from "@material-ui/core/es/MenuItem/MenuItem";
import Input from '@material-ui/core/Input';
import {TextField} from "@material-ui/core";
import { sha256, sha224 } from 'js-sha256';
import Snackbar from "@material-ui/core/Snackbar";

import config from 'react-global-configuration';

const cookies = new Cookies();

var querystring = require('querystring');

const DialogTitle = withStyles(theme => ({
    root: {
        borderBottom: `1px solid ${theme.palette.divider}`,
        margin: 0,
        padding: theme.spacing.unit * 2,
    },
    closeButton: {
        position: 'absolute',
        right: theme.spacing.unit,
        top: theme.spacing.unit,
        color: theme.palette.grey[500],
    },
}))(props => {
    const {children, classes, onClose} = props;
    return (
        <MuiDialogTitle disableTypography className={classes.root}>
            <Typography variant="h6">{children}</Typography>
        </MuiDialogTitle>
    );
});

const DialogContent = withStyles(theme => ({
    root: {
        margin: 0,
        padding: theme.spacing.unit * 2,
    },
}))(MuiDialogContent);

const DialogActions = withStyles(theme => ({
    root: {
        borderTop: `1px solid ${theme.palette.divider}`,
        margin: 0,
        padding: theme.spacing.unit,
    },
}))(MuiDialogActions);

const styles = theme => ({
    root: {
        width: '100%',
        marginTop: theme.spacing.unit * 3,
        overflowX: 'auto',
    },
    textField: {
        marginLeft: "10px",
        marginRight: "10px",
    },
    select: {
    },
    close: {
        padding: theme.spacing.unit / 2,
    },
});

let id = 0;

function createData(name, email, userType, status) {
    id += 1;
    return {id, name, email, userType, status};
}

class ManageUsers extends Component {
    constructor(props) {
        super(props);
        this.state = {
            data: [],
            rowData: [],
            circularProgressVis: "",
            dialogOpen: false,
            dialogOpenId: 0,
            dialogOpenUser: {},
            added: false,
            dialogInfo: {
                userType: "",
                enabled: true,
                name: "",
            },
            messageBox: {
                text: "",
                open: false
            }
        };
        this.getUsers();
    }

    getUsers() {
        var t = this;
        axios.get(config.get("apiUrl") + '/users?token=' + cookies.get("token") + "&all=").then(function (response) {
            console.log(response.data);
            if (!t.state.added) {
                t.setState({rowData: [], data: [], circularProgressVis: "visible"});
                var ret = response.data;

                var output = Object.keys(ret).map(function (key) {
                    return {type: key, name: ret[key]};
                });

                var o = [];

                output.forEach(function (el) {
                    var n = "";
                    if (el.name.enabled) {
                        n = "Enabled";
                    } else
                        n = "Disabled";
                    o.push(createData(el.name.name, el.name.email, el.name.userType, n));
                });

                t.setState({rowData: o, data: ret, circularProgressVis: "hidden"});
            }

            return o;
        });
    }

    handleRowClick(id) {
        console.log(id);
        this.setState({dialogOpen: true, dialogOpenId: id, });
        var g = 0;
        for (var k in this.state.data) {
            var d = this.state.data[k];
            g++;
            if (id === g) {
                this.setState({dialogOpenUser: d, dialogInfo: { userType: d.userType, enabled: d.enabled, name: d.name, uid: d.uid }});
            }
            console.log(d);
        }
    }

    handleDialogClose(t) {
        this.setState({dialogOpen: false, dialogOpenId: id,});
    }

    userTypeInHandleChange = event => {
        console.log(event.target.value);
        this.setState({ dialogInfo: { userType: event.target.value, enabled: this.state.dialogInfo.enabled } });
    };

    enabledStateHandlerChange = event => {
        this.setState({ dialogInfo: { enabled: !this.state.dialogInfo.enabled, userType: this.state.dialogInfo.userType } });
    };

    handleDialogSave(t) {
        var t = this;

        const headers = {
            'Content-Type': 'application/x-www-form-urlencoded;charset=UTF-8'
        };

        axios.put(config.get("apiUrl") + '/users/' + this.state.dialogOpenUser.email + '?token=' + cookies.get("token"),
            querystring.stringify({
                name: this.state.dialogOpenUser.name,
                userType: this.state.dialogInfo.userType,
                enabled: (!this.state.dialogInfo.enabled),
            }),headers).then(function (response) {
            var ret = response.data;

            console.log(t.state.dialogOpenUser);
            console.log(t.state.dialogInfo);
            console.log(ret);

            t.getUsers();

            t.setState({ messageBox: {open: true, text: "Updated"}, dialogOpen: false });

        });
    }

    render() {
        var t = this;


        return (
            <div>
                <Table className={styles.table}>
                    <TableHead>
                        <TableRow>
                            <TableCell>Name</TableCell>
                            <TableCell align="right">Email</TableCell>
                            <TableCell align="right">Type</TableCell>
                            <TableCell align="right">Status</TableCell>
                        </TableRow>
                    </TableHead>
                    <TableBody>
                        {this.state.rowData.map(row => {
                            if (!this.state.added) {
                                return (
                                    <TableRow key={row.id + '1'} hover className={styles.tableRow}
                                              onClick={(event) => this.handleRowClick(row.id)}>
                                        <TableCell key={row.id + '2'} component="th" scope="row">{row.name}</TableCell>
                                        <TableCell key={row.id + '3'} align="right">{row.email}</TableCell>
                                        <TableCell key={row.id + '4'} align="right">{row.userType}</TableCell>
                                        <TableCell key={row.id + '5'} align="right">{row.status}</TableCell>
                                    </TableRow>
                                );
                            }
                        })}
                    </TableBody>
                </Table>
                <Dialog
                    disableEnforceFocus
                    onClose={(e) => this.handleDialogClose(t)}
                    aria-labelledby="customized-dialog-title"
                    open={this.state.dialogOpen}
                >

                    <DialogTitle id="customized-dialog-title" onClose={(e) => this.handleDialogClose(t)}>
                        {this.state.dialogOpenUser.email}
                    </DialogTitle>
                    <DialogContent>

                        <div>
                            <Select
                                className={styles.select}
                                value={this.state.dialogInfo.userType}
                                onChange={this.userTypeInHandleChange}>
                                <MenuItem value={"USER"}>USER</MenuItem>
                                <MenuItem value={"ADMIN"}>ADMIN</MenuItem>
                            </Select>

                            <TextField
                                className={styles.textField}
                                id="name"
                                label="Name"
                                onChange={(e) => {
                                    var b = this.state.dialogOpenUser;
                                    b.name = e.target.value;
                                    this.setState({ dialogOpenUser: b });
                                }}
                                value={this.state.dialogOpenUser.name}
                                margin="normal"/>

                            <FormControlLabel
                                control={
                                    <Checkbox
                                        checked={this.state.dialogInfo.enabled}
                                        onChange={this.enabledStateHandlerChange}
                                        value="enabled"
                                    />
                                }
                                label="Enabled"
                            />

                        </div>

                    </DialogContent>
                    <DialogActions>
                        <Button onClick={(e) => this.handleDialogSave(t)} color="primary">
                            Save changes
                        </Button>
                    </DialogActions>
                </Dialog>
                <CircularProgress
                    style={{"margin": "theme.spacing.unit * 2", "visibility": this.state.circularProgressVis}}/>

            </div>
        )
    }
}


export default ManageUsers;
