import GridList from "@material-ui/core/GridList";
import GridListTile from "@material-ui/core/GridListTile";
import CircularProgress from "@material-ui/core/CircularProgress";
import ListItem from "@material-ui/core/es/ListItem/ListItem";
import List from "@material-ui/core/List";
import Blur from 'react-css-blur'
import Grid from "@material-ui/core/Grid";
import DeleteIcon from 'material-ui-icons/Delete';
import classNames from 'classnames'
import DropzoneComponent from 'react-dropzone-component';
import Image from 'material-ui-image'

var querystring = require('querystring');

const cookies = new Cookies();

var files = [];
var filesUrl = [];

const styles = theme => ({
    button: {
        margin: theme.spacing.unit,
    },
    textField: {
        marginLeft: theme.spacing.unit,
        marginRight: theme.spacing.unit,
        width: 200,
    },
    textarea: {
        width: "100%",
        height: "100%",
        minHeight: "100%"
    },
    input: {
        display: 'none',
    },
    container: {
        display: 'flex',
        flexWrap: 'wrap',
    },
    gridList: {
        width: 500,
        height: 450,
    },

    removeBtn: {
        transition: '.5s ease',
        position: 'absolute',
        opacity: 0,
        top: -5,
        right: -5,
        width: 40,
        height: 40
    },
    smallPreviewImg: {
        height: 100,
        width: 'initial',
        marginTop: 5,
        marginRight: "50px",
        color: 'rgba(0, 0, 0, 0.87)',
        transition: 'all 450ms cubic-bezier(0.23, 1, 0.32, 1) 0ms',
        boxSizing: 'border-box',
        boxShadow: 'rgba(0, 0, 0, 0.12) 0 1px 6px, rgba(0, 0, 0, 0.12) 0 1px 4px',
        borderRadius: 2,
        zIndex: 5,
        opacity: 1
    },
    imageContainer: {
        position: 'relative',
        zIndex: 10,
        textAlign: 'center',
        '&:hover $smallPreviewImg': {
            opacity: 0.3
        },
        '&:hover $removeBtn': {
            opacity: 1
        },
    }
});


class AddProduct extends Component {
    constructor(props) {
        super(props);
        this.state = {
            title: '',
            price: '',
            amount: '',
            text: '',
            filesUrl: [],
            files: [],
            messageBox: {
                text: "",
                open: false
            },
            dialogOpen: false,
            loadingDialogOpen: false,
            image: '',
            imageId: -1,
            imgStyle: [],
            handleImgRemoveEnab: false,
            girdVisible: [],
        };
    }

    handleAddProduct = () => {
        var data = {
            title: this.state.title,
            price: this.state.price,
            amount: this.state.amount,
            filesUrl: this.state.filesUrl,
            text: this.state.text,
            image: this.state.image,
            //token: cookies.get("token")
        };
        var t = this;
        if (data.filesUrl.length === 0) {
            this.setState({messageBox: {open: true, text: "Images is required"}});
        } else if (data.title === '') {
            this.setState({messageBox: {open: true, text: "Title is required"}});
        } else if (data.price === '') {
            this.setState({messageBox: {open: true, text: "Price is required"}});
        } else if (data.amount === '') {
            this.setState({messageBox: {open: true, text: "Amount is required"}});
        } else if (data.text === '') {
            this.setState({messageBox: {open: true, text: "Text is required"}});
        } else {
            if (data.image === '') {
                data.image = data.filesUrl[0];
            }
            console.log(data);

            axios.post(config.get("apiUrl") + '/product?token=' + cookies.get("token"), querystring.stringify(data)).then(function (response) {
                console.log(response.data);
                window.location.pathname = "";
            }).catch(function (error) {
                console.log(error.response);
                t.setState({messageBox: {open: true, text: error.response.data.error}});
            });
        }
    };

    handleClose() {
        this.setState({
            open: false
        });
    }

    async handleSave(files1) {
        //Saving files to state for further use and closing Modal.
        this.setState({
            open: false,
            imageStandId: -1,
        });
        files = files1;

        console.log(files);

        this.setState({loadingDialogOpen: true});
        var t = this;
        for (let i = 0; i < files.length; i++) {
            const data = new FormData();
            data.append('file', files[i]);
            data.append('filename', files[i].name);
            data.append('token', cookies.get("token"));

            var response = await axios.post(config.get("apiUrl") + '/upload', data)/*.then(function (response) {
                console.log(response.data.file);
                t.state.filesUrl.push({file: config.get('apiUrl') + response.data.file});
                filesUrl.push({file: config.get('apiUrl') + response.data.file});
            }).catch(function (error) {
                console.log(error.response);
            });*/
            console.log(response.data.file);
            t.state.filesUrl.push(response.data.file);
            t.state.files.push(response.data.file);
            filesUrl.push(response.data.file);
            t.state.imgStyle.push({});
        }
        var d = this.state.filesUrl;
        this.setState({loadingDialogOpen: false, filesUrl: d});
        //filesUrl = this.state.filesUrl;
        console.log(this.state.filesUrl);
        this.render();
    }

    handleOpen() {
        this.setState({
            open: true
        });
    }

    handleChange = name => event => {
        this.setState({
            [name]: event.target.value,
        });
    };

    handleDialogSave = event => {
        this.setState({
            dialogOpen: false
        });
    };

    handleDialogClose = event => {
        this.setState({
            dialogOpen: false
        });
    };

    handleImgRemove(i) {
        console.log(i);
        var d = this.state;
        d.filesUrl.splice(i, 1);
        d.files.splice(i, 1);
        this.setState(d);
        this.render();
    }

    render() {
        const {classes, theme} = this.props;

        let list = () => (
            <Grid container>
                {
                    this.state.filesUrl.map((fileObject, i) => {
                        const img = (/*isImage(fileObjeccard onclickt.file) ?*/
                            <Image className={classes.smallPreviewImg} role="presentation"
                                   src={config.get("apiUrl") + fileObject} style={this.state.imgStyle[i]}/>
                        );

                        let html = (
                            <Grid item xs={4} key={i} className={classes.imageContainer} onClick={() => {
                                var d = this.state.imgStyle;
                                //this.setState({imgStyle: []});
                                //var d = [];
                                var id = i;

                                console.log(d);
                                if (this.state.imageId === i) {
                                    d[i] = {};
                                    id = -1;
                                } else {
                                    d[i] = {border: "solid"};
                                }
                                console.log(d);
                                if (this.state.imageId !== -1) {
                                    d[this.state.imageId] = {};
                                }
                                console.log(d);
                                this.setState({imgStyle: d, imageId: id, image: fileObject});
                                this.render();
                                console.log(i);
                            }}>
                                {img}

                                <Fab onClick={() => {
                                    this.handleImgRemove(i)
                                }}
                                     aria-label="Delete"
                                     className={classes.removeBtn}>
                                    <DeleteIcon/>
                                </Fab>
                            </Grid>
                        );

                        if (this.state.girdVisible[i] === false) {
                            html = (<div key={i + ":1"}/>);
                        }
                        return (
                            <div key={i + ":2"}>
                                {html}
                            </div>
                        );
                    })
                }
            </Grid>
        );

        return (
            <div className="container">
                <form className={classes.container} noValidate autoComplete="off" onSubmit={this.handleAddProduct}>
                    <div>
                        <TextField
                            required
                            id="standard-title"
                            label="Title"
                            className={classes.textField}
                            value={this.state.title}
                            onChange={this.handleChange("title")}
                        />
                        <TextField
                            required
                            id="standard-number"
                            type="number"
                            label="Price"
                            className={classes.textField}
                            value={this.state.price || ''}
                            onChange={this.handleChange("price")}
                        />
                        <TextField
                            required
                            id="standard-number"
                            type="number"
                            label="Amount"
                            className={classes.textField}
                            value={this.state.amount || ''}
                            onChange={this.handleChange("amount")}
                        />
                        <Button variant="contained" className={classes.button} color="primary"
                                onClick={this.handleAddProduct}>
                            Add
                        </Button>

                        <div>
                            <Button variant="contained" className={styles.button}
                                    onClick={() => (this.setState({dialogOpen: true}))}>
                                Images
                            </Button>
                            <Dialog
                                disableEnforceFocus
                                onClose={(e) => this.handleDialogClose()}
                                aria-labelledby="customized-dialog-title"
                                open={this.state.dialogOpen}
                            >
                                <DialogContent>

                                    <div>
                                        <input
                                            accept="image/*"
                                            className={classes.input}
                                            id="contained-button-file"
                                            multiple
                                            type="file"
                                            onChange={(e) => {
                                                this.handleSave(e.target.files)
                                            }}
                                        />
                                        <label htmlFor="contained-button-file">
                                            <Button variant="contained" component="span" className={classes.button}>
                                                Upload
                                            </Button>
                                        </label>

                                        <div>
                                            {list()}
                                        </div>
                                    </div>

                                </DialogContent>
                                <DialogActions>
                                    <Button onClick={(e) => this.handleDialogSave()} color="primary">
                                        Change
                                    </Button>
                                </DialogActions>
                            </Dialog>

                        </div>
                        <div>

                            <Dialog
                                disableEnforceFocus
                                aria-labelledby="customized-dialog-title"
                                open={this.state.loadingDialogOpen}
                            >
                                <CircularProgress className={classes.progress}/>
                            </Dialog>
                        </div>
                        <br/>
                        <div style={{"height": "500px"}}>
                            <textarea
                                required
                                value={this.state.text}
                                onChange={this.handleChange("text")}
                                className={classes.textarea}
                            >
                            </textarea>
                        </div>
                    </div>
                </form>

                <Snackbar
                    anchorOrigin={{
                        vertical: 'bottom',
                        horizontal: 'left',
                    }}
                    open={this.state.messageBox.open}
                    autoHideDuration={6000}
                    onClose={(event, reason) => {
                        if (reason === 'clickaway') {
                            return;
                        }

                        this.setState({messageBox: {open: false}});
                    }}
                    ContentProps={{
                        'aria-describedby': 'message-id',
                    }}
                    message={<span id="message-id">{this.state.messageBox.text}</span>}
                    action={[
                        <IconButton
                            key="close"
                            aria-label="Close"
                            color={"#fafafa"}
                            className={styles.close}
                            onClick={(event, reason) => {
                                if (reason === 'clickaway') {
                                    return;
                                }

                                this.setState({messageBox: {open: false}});
                            }}
                        >
                            <CloseIcon color={"#fafafa"}/>
                        </IconButton>,
                    ]}
                />
            </div>
        );
    }
}


AddProduct.propTypes = {
    classes: PropTypes.object.isRequired,
};

//export default Home;

export default withStyles(styles, {withTheme: true})(AddProduct);