import React from 'react';
import ReactDOM from 'react-dom';
import {BrowserRouter as Router, Route, Link, Switch} from "react-router-dom";
import './index.css';
import App from './App';
import * as serviceWorker from './serviceWorker';
import MuiThemeProvider from 'material-ui/styles/MuiThemeProvider';
import axios from "axios";
import ManageUsers from "./components/ManageUsers/ManageUsers";
import Cookies from 'universal-cookie'

import config from 'react-global-configuration';
import ProductPage from "./components/ProductPage/ProductPage";
import {isIE} from 'react-device-detect';

const cookies = new Cookies();

config.set({
    apiUrl: "http://localhost:8000",
});

var html = {};
var auth = false;
var t = this;

if (cookies.get("token") !== undefined) {
    axios.get(config.get("apiUrl") + '/users?token=' + cookies.get("token")).then(function (response) {
        var ret = response.data;
        console.log(response);
        console.log(ret);
        if (ret !== null) {

        } else {
            cookies.remove("token");
            //t.show("login");
            //window.location.reload();
        }
    }).catch(function (error) {
        cookies.remove("token");
    });
}

function notFound() {
    return (
        <div>
            404 page not found
        </div>
    )
}

if (!isIE) {
    html =
        <MuiThemeProvider>
            <Router>
                <Switch>
                        <Route exact path="/" component={(s) => rend("home")}/>
                        <Route path="/manageUsers" component={(s) => rend("manageUsers")}/>
                        <Route path="/registrer" component={(s) => rend("registrer")}/>
                        <Route path="/login" component={(s) => rend("login")}/>
                        <Route path="/addProduct" component={(s) => rend("addProduct")}/>
                        <Route path="/product/:id" component={(s) => rend("product/" + s.match.params.id)}/>
                        <Route path="/productEdit/:id" component={(s) => rend("productEdit/" + s.match.params.id)}/>
                        <Route component={notFound}/>
                </Switch>
            </Router>
        </MuiThemeProvider>

} else {
    html = <div>Internet Explorer is not supported</div>;
}
ReactDOM.render(
    <div>{html}</div>
    , document.getElementById('root'));

function rend(str) {
    return (
        <App rend={str}/>
    )
}

// If you want your app to work offline and load faster, you can change
// unregister() to register() below. Note this comes with some pitfalls.
// Learn more about service workers: http://bit.ly/CRA-PWA
serviceWorker.unregister();
